body{
    text-align:center;
    padding: 10px 0px;
    background: white;
}

.img{
    width: 88px;
    height: 88px;
    border-radius: 50%;
    margin-bottom: 5px;
}

.links{
    width:100%;
    margin: 0 auto;
}

.btn{
    box-sizing: border-box;

/* Auto layout */

display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
padding: 24px 32px;
gap: 8px;
font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 28px;

width: 80%;
margin: 0 auto;
height: 76px;

/* Gray/200 */

background: #EAECF0;
/* Gray/200 */

border: 1px solid #EAECF0;
border-radius: 8px;

/* Inside auto layout */

flex: none;
order: 0;

text-decoration:none;
}

.btn input{

/* Gray/900 */
color: #101828;
border: none;
background: #EAECF0;

}

.socials{
    margin-top: 10px;
}
.social{
    margin-right:20px;
}
p{
 font-size: 18px;
 font-weight:bold;
}

@media only screen and (max-width: 400px){
    .btn{
        height:68px;
        font-size: 13px;
    }
}